import React, { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { connect } from "./redux/blockchain/blockchainActions";
import { fetchData } from "./redux/data/dataActions";
import * as s from "./styles/globalStyles";
import styled from "styled-components";
import i1 from "./assets/images/1.jpg";
import Box from '@mui/material/Box';
import Slider from '@mui/material/Slider';

export const StyledButton = styled.button`
  padding: 10px;
  border-radius: 50px;
  border: none;
  background-color: #ffffff;
  padding: 10px;
  font-weight: bold;
  color: #000000;
  width: 100px;
  cursor: pointer;
  box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  -webkit-box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  -moz-box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  :active {
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
  }
`;

export const ResponsiveWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: stretched;
  align-items: stretched;
  width: 100%;
  @media (min-width: 767px) {
    flex-direction: row;
  }
`;

export const StyledImg = styled.img`
  width: 200px;
  height: 200px;
  @media (min-width: 767px) {
    width: 350px;
    height: 350px;
  }
  transition: width 0.5s;
  transition: height 0.5s;
`;
const marks = [
  {
    value: 1,
    label: '1',
  },
  {
    value: 2,
    label: '2',
  },
  {
    value: 3,
    label: '3',
  },
  {
    value: 4,
    label: '4',
  },
  {
    value: 5,
    label: '5',
  },
];

function valuetext(value) {
  return `${value}`;
}

function App() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const [feedback, setFeedback] = useState("Get your ticket now!");
  const [claimingNft, setClaimingNft] = useState(false);
  const [value, setValue] = useState(1);
  const changeValue = (event, value) => {
    setValue(value);
  };

  const claimNFTs = (_amount) => {
    if (_amount <= 0) {
      return;
    }
    setFeedback("Minting your 11Minutes Beta Game Ticket");
    setClaimingNft(true);
    blockchain.smartContract.methods
      .mint(blockchain.account, _amount)
      .send({
        gasLimit: (180000 * _amount).toString(),
        to: "0x55C02523D6098B8da35dC3F7c9ee8984a3Ff9F8F",
        from: blockchain.account,
        value: blockchain.web3.utils.toWei((0 * _amount).toString(), "ether"),
      })
      .once("error", (err) => {
        console.log(err);
        setFeedback("Sorry, something went wrong please try again later.");
        setClaimingNft(false);
      })
      .then((receipt) => {
        setFeedback(
          "WOW, you now own a 11Minutes Beta Game Ticket. Go visit Opensea.io to view it."
        )
        .addLink("https://opensea.io/account");
        setClaimingNft(false);
        dispatch(fetchData(blockchain.account));
      });
  };

  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };

  useEffect(() => {
    getData();
  }, [blockchain.account]);

  return (
    <s.Screen style={{ backgroundColor: "transparent" }}>
      <s.Container flex={1} ai={"center"} style={{ padding: 24 }}>
        <s.TextTitle
          style={{ textAlign: "center", fontSize: 30, fontWeight: "bold" }}
        >
          Mint a 11Minutes Beta Game Ticket
        </s.TextTitle>
        <s.SpacerMedium />
        <ResponsiveWrapper flex={1} style={{ padding: 24 }}>
        <s.Container flex={1} jc={"center"} ai={"center"}>
            <s.TextTitle
              style={{ textAlign: "center", fontSize: 35, fontWeight: "bold" }}
            >
              How do I mint?
            </s.TextTitle>        
            <s.TextDescription style={{ textAlign: "center", fontSize: 15 }}>
            1. Connect your wallet!<br/>
            2. If not already done, swith to Polygon Mainnet <br/>
            3. Press on "Get Ticket"<br/>
            4. Confirm the Transaction in your wallet!
          </s.TextDescription>
          <s.SpacerSmall />
          <s.TextDescription style={{ textAlign: "center", fontSize: 15 }}>
            If you not have any MATIC in your wallet for paying the gas fee, you can get some here.<br/><br/>
            <a
              target={"_blank"}
              href={"https://www.binance.com/en/trade/MATIC_USDT"}
              style={{ color: "#FB7700", fontSize: 15 }}
              >
             Binance
            </a>
            &nbsp;|&nbsp;
            <a
              target={"_blank"}
              href={"https://www.kraken.com/prices/matic-polygon-price-chart/usd-us-dollar?interval=1m"}
              style={{ color: "#FB7700", fontSize: 15 }}
              >
             Kraken
            </a>
            &nbsp;|&nbsp;
            <a
              target={"_blank"}
              href={"https://www.gate.io/en/trade/MATIC_USDT"}
              style={{ color: "#FB7700", fontSize: 15 }}
              >
             Gate.io
            </a>
            &nbsp;|&nbsp;
            <a
              target={"_blank"}
              href={"https://www.okex.com/de/trade-spot/matic-usdt"}
              style={{ color: "#FB7700", fontSize: 15 }}
              >
             Okex
            </a>
          </s.TextDescription>
          </s.Container>
          <s.SpacerMedium />
          <s.Container flex={1} jc={"center"} ai={"center"}>
            <StyledImg alt={"example"} src={i1} />
            <s.SpacerMedium />
            <s.TextTitle
              style={{ textAlign: "center", fontSize: 35, fontWeight: "bold" }}
            >
              {data.totalSupply !== 0 ? (
              (data.totalSupply) + '/10000') : null}

            </s.TextTitle>
          </s.Container>
          <s.SpacerMedium />
          <s.Container
            flex={1}
            jc={"center"}
            ai={"center"}
            style={{ padding: 24 }}
          >
            {Number(data.totalSupply) == 10000 ? (
              <>
                <s.TextTitle style={{ textAlign: "center" }}>
                  The sale has ended.
                </s.TextTitle>
                <s.SpacerSmall />
                <s.TextDescription style={{ textAlign: "center" }}>
                  You can maybe find 11Minutes Beta Game Ticekts on{" "}
                  <a
                    target={"_blank"}
                    href={"https://opensea.io/collection/11Minutes-Beta-Game"}
                  >
                    Opensea.io
                  </a>
                </s.TextDescription>
              </>
            ) : (
              <>
                <s.TextTitle style={{ textAlign: "center", fontSize: 25 }}>
                  The Beta Game Tickets are free of charge.
                </s.TextTitle>
                <s.SpacerXSmall />
                <s.TextDescription style={{ textAlign: "center", fontSize: 20 }}>
                  Excluding gas fee.
                </s.TextDescription>
                <s.SpacerSmall />
                <s.TextDescription style={{ textAlign: "center", fontSize: 20, textDecoration: "underline"}}>
                  {feedback}
                </s.TextDescription>
                <s.SpacerMedium />
                {blockchain.account === "" ||
                blockchain.smartContract === null ? (
                  <s.Container ai={"center"} jc={"center"}>
                    <s.TextDescription style={{ textAlign: "center", fontSize: 20 }}>
                      Connect your wallet and make sure you are on Polygon Mainnet.
                    </s.TextDescription>
                    <s.SpacerSmall />
                    <StyledButton
                      onClick={(e) => {
                        e.preventDefault();
                        dispatch(connect());
                        getData();
                      }}
                    >
                      CONNECT
                    </StyledButton>
                    {blockchain.errorMsg !== "" ? (
                      <>
                        <s.SpacerSmall />
                        <s.TextDescription style={{ textAlign: "center" }}>
                          {blockchain.errorMsg}
                        </s.TextDescription>
                      </>
                    ) : null}
                  </s.Container>
                ) : (
                  <s.Container ai={"center"} jc={"center"} fd={"row"}>
                     <Box sx={{ width: 200, paddingTop: "20px", paddingRight: "10px"}}>
                      <Slider
                        style={{color: "#FB7700"}}
                        aria-label="Custom marks"
                        defaultValue={1}
                        getAriaValueText={valuetext}
                        step={null}
                        min={1}
                        max={5}
                        valueLabelDisplay="on"
                        marks={marks}
                        disabled={claimingNft ? 1 : 0}
                        onChange={changeValue}
                      />
                    </Box>
                    <StyledButton
                      disabled={claimingNft ? 1 : 0}
                      onClick={(e) => {
                        e.preventDefault();
                        claimNFTs(value);
                        getData();
                      }}
                    >
                      {claimingNft ? "BUSY" : "Get Ticket"}
                    </StyledButton>
                    <s.SpacerSmall />
                    <StyledButton
                      onClick=
                      {(e) => {
                        e.preventDefault();                        
                        window.open('https://elevenminutes.io/beta-game','_blank');
                      }}
                    
                    >
                      PLAY
                    </StyledButton>
                  </s.Container>
                )}
              </>
            )}
          </s.Container>
        </ResponsiveWrapper>
        <s.SpacerSmall />
        <s.Container jc={"center"} ai={"center"} style={{ width: "70%" }}>
          <s.TextDescription style={{ textAlign: "center", fontSize: 15 }}>
            Please make sure you are connected to the right network (Polygon
            Mainnet) and the correct address. Please note: Once you make the
            purchase, you cannot undo this action.
          </s.TextDescription>
          <s.SpacerSmall />
          <s.TextDescription style={{ textAlign: "center", fontSize: 15 }}>
            We have set the gas limit to 180000 for each NFT mindted to successfully
            mint your NFTs. We recommend that you don't change the gas limit.
          </s.TextDescription>
        </s.Container>
      </s.Container>
    </s.Screen>
  );
}

export default App;
